  <router-view></router-view>
  <div class="main-app" v-if="page == 'home'">

    <header>
      <div class="title">ÎÇòÏóêÍ≤å Ïñ¥Ïö∏Î¶¨Îäî Ï∂ïÍµ¨ÌåÄ</div>
    </header>
    <div class="tips">
      <div class="tip">
        <div class="tip-img">‚öΩ</div>
        <div class="tip-text">Premier LeagueÏùò BIG6 ÌåÄÎßå Ï∂îÏ≤úÌï©ÎãàÎã§</div>
      </div>
      <div class="tip">
        <div class="tip-img">üõ∏</div>
        <div class="tip-text">ÏßàÎ¨∏Ïóê Í≥µÏã†Î†•ÏùÄ ÏóÜÏäµÎãàÎã§</div>
