                <input v-if="b==1" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="leftMaxClass" />
                <input v-if="b==2" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="leftMinClass" />
                <input v-if="b==3" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="midClass" />
                <input v-if="b==4" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="rightMinClass" />
                <input v-if="b==5" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="rightMaxClass" />
              </div>
            </template>
