          </div>
        </div>
        <div v-else :class="class2" ref="a.id">
          <div class="content">{{a.content}}</div>
          <div class="radiogroup" checked="false">
            <div class="left-option">{{a.leftOption}}</div>
            <template v-for="b in 5" :key="b">
              <div class="radio-container">
                <input v-if="b==1" v-bind:value="str(a.id, b)" v-bind:name="result(a.id)" @change="[select($event)]" type="radio" :class="leftMaxClass" />
