<script>


import axios from 'axios'
import nv from './components/navbar-component'
import ft from './components/footer-component'
import load from './components/loading-component.vue'
import result from './components/result-component.vue'
// import error from '@/components/error-component.vue'
import questions from './assets/question.js';


export default {
  name: 'App',
  //Îç∞Ïù¥ÌÑ∞ Î≥¥Í¥Ä
  data(){
    return {
      
      test1 : [],
      class1: 'question',
      class2: 'question inactive',
      leftMaxClass : 'form-check-input left max',
      leftMinClass : 'form-check-input left min',
      midClass : 'form-check-input mid',
      rightMinClass : 'form-check-input right min',
      rightMaxClass : 'form-check-input right max',
      a : 1,
      b : 2,
      questions : questions,
      percentage : 0,
      id : 0,
      filterWidth : '',
      page : 'home',
      result1 : '',
      result2 : '',
      result3 : '',
      result4 : '',
      result5 : '',
      result6 : '',
      result7 : '',
      result8 : '',
      result9 : '',
      result10 : '',
      teamName : '',
    }
  },
  
  // function ÎßåÎìúÎäî Í≥≥
  // Îç∞Ïù¥ÌÑ∞Î•º Ïì∞Î†§Î©¥ thisÎ•º Ïç®Ï§òÏïºÌï®
  methods : {
    
    str : function(a,b){
      var str = "";
      str += a;
      str += b;
      // console.log(str);
      // this.vBind = str;
      return str;
      
    },

    result : function(a){
      var res = "result";
      res += a;
      return res;
    },

    scroll : function(){
        
        const radioGroup = document.getElementsByClassName("radiogroup");
        // console.log(radioGroup);
        var bool = this.radioCheck(radioGroup);

        if(bool != true){
            radioGroup[bool].scrollIntoView({behavior:"smooth", block:"center"});
        }else{
            axios.post('https://heavy-pug-9.loca.lt/result',null,{params:{"result1" : this.result1,
            "result2" : this.result2,
            "result3" : this.result3,
            "result4" : this.result4,
            "result5" : this.result5,
            "result6" : this.result6,
            "result7" : this.result7,
            "result8" : this.result8,
            "result9" : this.result9,
            "result10" : this.result10,}})
            .then(res => {
                this.teamName = res.data;
                console.log(res);
            })
            this.page = 'load';
        }

        
        
    },
    makeWidth : function(){
        var str = 'width :';
        str += this.percentage;
        str += '%';
        this.filterWidth = str;
    },
    select : function(event){
        const target = event.target;
        var valueId = target.getAttribute('value');
        
        console.log(valueId.substring(0,1))
        console.log(target.getAttribute('value'));
        console.log(target.getAttribute('name'));
        // console.log(target.parentElement.parentElement.parentElement.nextElementSibling);
        if(valueId.length != 3){

            switch(valueId.substring(0,1)){
                case '1' : this.result1 = valueId;
                break;
                case '2' : this.result2 = valueId;
                break;
                case '3' : this.result3 = valueId;
                break;
                case '4' : this.result4 = valueId;
                break;
                case '5' : this.result5 = valueId;
                break;
                case '6' : this.result6 = valueId;
                break;
                case '7' : this.result7 = valueId;
                break;
                case '8' : this.result8 = valueId;
                break;
                case '9' : this.result9 = valueId;
                break;
                
            }
        }else{
            this.result10 = valueId;
        }

        const active = target.parentElement.parentElement.parentElement;
        active.classList.add('inactive');
        const next = target.parentElement.parentElement.parentElement.nextElementSibling;
        active.classList.remove('inactive');
        next.scrollIntoView({behavior:"smooth", block:"center"});
        var radioGroup = target.parentElement.parentElement;
        radioGroup.setAttribute('checked', 'true');
        this.calcPer();
    },
    radioCheck : function(checked){
        var arr = [];
        for(var a1=0; a1<checked.length; a1++){
            arr[a1] = checked[a1].getAttribute('checked')
        }

        for(var a2=0; a2<arr.length; a2++){
            if(arr[a2] == 'false'){
                
                return a2;
            }
        }
        return true;
    },
    calcPer : function(){
        var radioGroup = document.getElementsByClassName("radiogroup");
        var arr = [];
        for(var t=0; t<radioGroup.length; t++){
            arr[t] = radioGroup[t].getAttribute('checked');
            // console.log(arr[t])
        }
        
        this.percentage = 0;
        for(var j=0; j<arr.length; j++){
            if(arr[j] == 'true'){
                this.percentage += 10;
                this.makeWidth();
                
            }
        }
    },
    setPage : function(page){
        this.page = page;
        console.log(this.page);
    } 
  },

  components: {
    nv,
    ft,
    load,
    result,
    // error,
  },
  mounted(){
    

  }
  
}

</script>

