<template>
  <div class="loader">

  </div>
</template>

<script>
export default {
    methods : {
        goNext : function(){
            console.log("goNext");
            setTimeout(this.page, 3500);
        },
        page : function(){
            console.log("page")
            this.$emit("setPage", "result");
        }
    },
    mounted(){
        this.goNext();
        console.log("mounted")
    }
}
</script>

<style>
.loader{
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border: 16px solid #f3f3f3;
    border-top: 16px solid #3498db;
    border-radius: 50%;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
}

@-webkit-keyframes spin{
    0%{-webkit-transform: rotate(0deg);}
    100%{-webkit-transform: rotate(360deg);}
}

@keyframes spin{
    0%{transform: rotate(0deg);}
    100%{transform: rotate(360deg);}
}
</style>